import React from "react";
import "./news.scss";
import Section from "../Section";
import * as moment from "moment";
import data from "../../api/data";
import { markedInline } from "../../utils/helpers";
import "./Experience.scss";

function formatDate(str, _default) {
  const date = moment(str, moment.ISO_8601, false);
  if (date.isValid()) {
    return date.format("MMM YYYY");
  }
  return _default;
}

function Experience(props) {
  const { experience } = props;
  let job = experience.job;
  if (experience.advisor) {
    job = `${job} (advised by ${experience.advisor})`;
  }
  return (
    <tr>
      <td className="date">
        <span className="date-from">{formatDate(experience.from, experience.from)}</span>
        <span className="date-to">{formatDate(experience.to, experience.to)}</span>
      </td>
      <td className="content">
        <span className="institute" dangerouslySetInnerHTML={{ __html: markedInline(experience.company) }} />
        {experience.location}
        <br />
        <span className="title" dangerouslySetInnerHTML={{ __html: markedInline(job) }} />
        {experience.department &&
          <React.Fragment>
            {", "}
            <span className="department" dangerouslySetInnerHTML={{ __html: markedInline(experience.department) }} />
          </React.Fragment>
        }
      </td>
    </tr>
  );
}

class Experiences extends React.Component {
  render() {
    const { title, experiences } = this.props;
    return (
      <Section id="experiences">
        <h2 dangerouslySetInnerHTML={{ __html: markedInline(title) }} />
        {experiences && (
          <table className="experiences">
            <tbody>
              {experiences.map((item, i) => (
                <Experience experience={item} key={i}/>
              ))}
            </tbody>
          </table>
        )}
      </Section>
    );
  }
}

export default function DefaultExperience(props) {
  const experiences = data.experience || [];
  const title = "Research / Work Experience";
  return <Experiences title={title} experiences={experiences} {...props} />;
}
